* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  color: #333;
  background-color: #f5f5f5;
  background-image: url('C:\Users\RUDRAJIT DUTTA\Desktop\Appointments\appointments-app\src\assets\26807.jpg');
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  min-height: 100vh;
}

.App {
  min-height: 100vh;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.doctor-listing-container {
  padding-top: 20px;
}

.content-container {
  display: flex;
  margin-top: 30px;
  align-items: flex-start;
}

@media (max-width: 768px) {
  .content-container {
    flex-direction: column;
  }
  
  .doctor-list {
    padding-left: 0;
    margin-top: 20px;
  }
  
  .filter-panel {
    width: 100%;
  }
}
